<template>
    <v-container fluid class="container pa-0" @click="timer_logout_set()">
        <layout-rightNavi
            :naviItems="naviItems"
        />
        <v-col align="center">
            <v-row align="center" justify="center" class="report_wrapper">
                <v-col cols="8" align="center">
                    <p class="report_title">수고하셨습니다.</p>
                    <v-row align="center" class="report_recode_area">
                        <v-col align="center">
                            <v-row justify="center">
                                <img src="@/assets/images/play/icon_exercise.png">
                                <span class="report_recode_title">운동</span>
                            </v-row>
                            <v-col class="mt-1">
                                <span class="report_recode_text">{{recodeItems.exerciseSet}}/{{recodeItems.exerciseSet}}종</span>
                            </v-col>
                        </v-col>
                        <div>
                            <span class="report_recode_divide">|</span>
                        </div>
                        <v-col align="center">
                            <v-row justify="center">
                                <img src="@/assets/images/play/icon_timer.png">
                                <span class="report_recode_title">운동 시간</span>
                            </v-row>
                            <v-col class="mt-1">
                                <span class="report_recode_text">{{recodeItems.exerciseTime}}초</span>
                            </v-col>
                        </v-col>
                    </v-row>
                    <v-col cols="11" class="mt-3">
                        <template v-for="(item,i) in recodeItems.exerciseItems">
                            <v-row :key="i">
                                <div v-if="item.success" class="report_recode_item">
                                    <v-icon class="report_recode_item_success">check_circle</v-icon>
                                    <span class="report_recode_item_success_title">{{item.title}}</span>
                                    <span class="report_recode_item_success_recode">{{item.recode}}</span>
                                </div>
                                <div v-else class="report_recode_item">
                                    <v-icon class="report_recode_item_fail">cancel</v-icon>
                                    <span class="report_recode_item_fail_title">{{item.title}}</span>
                                    <span class="report_recode_item_fail_recode">{{item.recode}}</span>
                                </div>
                            </v-row>
                        </template>
                    </v-col>
                </v-col>
            </v-row>
        </v-col>
    </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import timer_logout from '@/mixins/timer_logout.js'

export default {
    name: 'Report',
    mixins:[ timer_logout ],
    data:()=>({
        naviItems:{},
        recodeItems:{
            totalSet: 5,
            exerciseSet: 4,
            exerciseTime: 50,
            exerciseItems:[
                // {
                //     title: '체스트프레스',
                //     success: true,
                //     recode: '2세트 | 36회'
                // },
                // {
                //     title: '팩 덱 플라이',
                //     success: true,
                //     recode: '60초'
                // },
                // {
                //     title: '덤벨 플라이',
                //     success: false,
                //     recode: '25초'
                // },
                // {
                //     title: '케이블 크로스 오버',
                //     success: false,
                //     recode: '0세트 | 0회'
                // },
                // {
                //     title: '원 암 트라이셉스 익스텐션',
                //     success: true,
                //     recode: '2세트 | 36회'
                // },
                // {
                //     title: '벤치',
                //     success: true,
                //     recode: '2세트 | 36회'
                // },
            ]
        }
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_MENU',
            'GET_REPORT_EXERCISE_TIME',
            'GET_REPORT_RESULT_DETAIL',
            'GET_REPORT_ITEMS',
        ])
    },
    mounted() {
        this.EditItems()
    },
    destroyed(){
        this.ResetPlayItems()
    },
    methods: {
        ...mapActions([
            'ACT_MENU_API_GET_EX_RESULT_LIST',
        ]),
        ...mapMutations([
            'MU_REPORT_EXERCISE_TIME',
            'MU_REPORT_RESULT_DETAIL',
            'MU_REPORT_RESULT_DETAIL_RESET',
            'MU_REPORT_ITEMS',
        ]),
        async CompleteExercise(){
            const exType = 0 // 프리웨이트
            await this.ACT_MENU_API_GET_EX_RESULT_LIST(exType)
            .then((resp)=>{
                console.log('report', resp)
            })
        },
        EditItems(){
            this.recodeItems = {
                totalSet: this.GET_REPORT_ITEMS.totalSet,
                exerciseSet: this.GET_REPORT_ITEMS.exerciseSet,
                exerciseTime: this.GET_REPORT_EXERCISE_TIME,
                exerciseItems: this.GET_REPORT_RESULT_DETAIL,
            }

            // 되돌아갈 Link
            let firstLink
            switch (this.GET_MENU_SELECT_MENU) {
                case '오늘의운동':
                    firstLink = 'today'
                    break;
                case '프리웨이트':
                    firstLink = 'freeWeight'
                    break;
                case '스트레칭':
                    firstLink = 'stretching'
                    break;
                case '서킷트레이닝':
                    firstLink = 'circuit'
                    break;
                case '요가':
                    firstLink = 'yoga'
                    break;
                default:
                    firstLink = '/user/main'
                    break;
            }
            
            this.naviItems = {
                buttonGroup:[
                    {
                        text:'처음으로',
                        link: '/user/main',
                    },
                    {
                        text:'종료',
                        link: firstLink,
                    }
                ]
            }
        },
        ResetPlayItems(){
            // 초기화
            const exerciseTime = '00:00'
            const playItems = {
                totalSet: 0,
                setStartTime: 0,
                exerciseSet: 0,
                exerciseTotalCount: 0,
                exerciseCount: 0,
                detailItems:[]
            }

            this.MU_REPORT_EXERCISE_TIME(exerciseTime)
            this.MU_REPORT_ITEMS(playItems)
            this.MU_REPORT_RESULT_DETAIL_RESET()
        },
    },
}
</script>

<style scoped lang="scss">
.report_wrapper{
    margin-top: 3rem;
    color: black;
    height: 80vh;
    overflow: auto;

    .report_title{
        font-size: var(--font-size-normal);
        color: white;
    }

    .report_recode_area{
        background-color: white;
        border-radius: 5rem;
        height: 150px;
        padding-top: 1rem;
    }

    .report_recode_title{
        line-height: 2rem;
        margin-left: 0.85rem;
    }
    .report_recode_text{
        font-size: var(--font-size-medium);
    }

    .report_recode_divide{
        font-size: var(--font-size-normal);
        font-weight: 300;
    }
    
    .report_recode_item{
        margin: 1rem 0;
        width: 100%;

        .report_recode_item_success{
            float: left;
            font-size: var(--font-size-medium);
            color: var(--primary-main);
            margin-right: 2rem;
        }

        .report_recode_item_success_title{
            float: left;
            font-size: var(--font-size);
            color: #c0c0c0;
        }
        
        .report_recode_item_success_recode{
            float: right;
            font-size: var(--font-size);
            color: #c0c0c0;
        }

        .report_recode_item_fail{
            float: left;
            font-size: var(--font-size-medium);
            color: var(--secondary);
            margin-right: 2rem;
        }

        .report_recode_item_fail_title{
            float: left;
            font-size: var(--font-size);
            color: var(--secondary);
        }

        .report_recode_item_fail_recode{
            float: right;
            font-size: var(--font-size);
            color: var(--secondary);
        }
    }
}
</style>